/* App.css */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #1a3263;
  color: rgb(136, 180, 241);
  font-family: Arial, sans-serif;
}

h1 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.digital-clock {
  font-size: 4rem;
  font-family: 'Courier New', monospace;
  color: #61dafb;
}
